<template>
  <!-- 注册页面的整体盒子 -->
  <div class="reg-container">
    <!-- 注册的盒子 -->
    <div class="reg-box">
      <!-- 标题的盒子 -->
      <div class="title-box">店铺后台管理系统</div>
      <!-- 注册的表单区域 -->
      <el-form ref="regForm" :model="form" :rules="regRules">
        <!-- 用户名 -->
        <el-form-item prop="username">
          <el-input
            placeholder="请输入用户名"
            v-model="form.username"
          ></el-input>
        </el-form-item>
        <!-- 用户密码 -->
        <el-form-item prop="password">
          <el-input
            placeholder="请输入密码"
            type="password"
            v-model="form.password"
          ></el-input>
        </el-form-item>
        <!-- 确认密码 -->
        <el-form-item prop="repassword">
          <el-input
            type="password"
            placeholder="请再次确认密码"
            v-model="form.repassword"
          ></el-input>
        </el-form-item>
        <!-- 店铺名称 -->
        <el-form-item prop="shopname">
          <el-input
            placeholder="请输入店铺名称"
            v-model="form.shopname"
          ></el-input>
        </el-form-item>
        <!-- 所属食堂 -->
        <el-form-item prop="roomID">
          <el-select
            v-model="form.roomID"
            placeholder="请选择店铺所属食堂"
            style="width: 100%"
          >
            <el-option
              v-for="room in roomList"
              :key="room.rid"
              :label="room.rname"
              :value="room.rid"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <!-- 店铺位置 -->
        <el-form-item prop="position">
          <el-input
            placeholder="请输入店铺位置"
            v-model="form.position"
          ></el-input>
        </el-form-item>
        <!-- 负责人电话 -->
        <el-form-item prop="tel">
          <el-input
            placeholder="请输入店铺负责人联系电话"
            v-model="form.tel"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button class="btn-reg" type="primary" @click="registerFn"
            >注册</el-button
          >
          <el-link type="info" @click="goLogin">去登陆</el-link>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
// 经验：
import { registerAPI, getRoomListAPI } from "@/api/index";
export default {
  name: "my-register",
  data() {
    return {
      // 表单的数据对象
      form: {
        username: "",
        password: "",
        repassword: "",
        usertype: "b",
        shopname: "",
        roomID: "",
        position: "",
        tel: "",
      },
      // 选择的食堂列表
      roomList: [],
      // 表单的规则对象
      regRules: {
        // 用户名
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          {
            pattern: /^[a-zA-Z0-9]{1,10}$/,
            message: "用户名只能是1-10的大小写字母和数字的组合",
            trigger: "blur",
          },
        ],
        tel: [
          { required: true, message: "请输入联系方式", trigger: "blur" },
          {
            pattern: /^[0-9]{11}$/,
            message: "请输入正确格式的联系方式！",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            pattern: /^\S{6,15}$/,
            message: "密码只能是6-15位的非空字符",
            trigger: "blur",
          },
        ],
        repassword: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            validator: this.samePwnFn,
            trigger: "blur",
          },
        ],
        shopname: [
          { required: true, message: "请输入店铺名称", trigger: "blur" },
          {
            min: 1,
            max: 10,
            message: "店铺名的长度应在 3 到 5 个字符",
            trigger: "blur",
          },
        ],
        roomID: [
          { required: true, message: "请选择所属食堂", trigger: "change" },
        ],
        position: [
          { required: true, message: "请输入店铺位置", trigger: "blur" },
          {
            min: 5,
            max: 30,
            message: "店铺位置信息的长度应在 5 到 30 个字符",
            trigger: "blur",
          },
        ],
      },
    };
  },
  //   =================================================以下是方法部分=================================================
  methods: {
    // 注册按钮的点击事件
    registerFn() {
      this.$refs.regForm.validate(async (valid) => {
        if (valid) {
          // 检验成功，准备发起请求
          // 编辑表单信息
          let form = {
            username: this.form.username,
            password: this.form.password,
            tel: this.form.tel,
            usertype: "b",
            sname: this.form.shopname,
            rid: this.form.roomID,
            location: this.form.position,
          };
          const { data: res } = await registerAPI(form);
          // 提示错误信息
          if (res.code !== 0) return this.$message.error(res.message);
          // 提示成功信息
          this.$message.success(res.message);
          // 注册成功后跳转至登录页面
          this.$router.push("/login");
        } else {
          return this.$message.error("表单数据有误，请重新编辑。");
        }
      });
    },
    // 去登陆的点击事件
    goLogin() {
      this.$router.push("/login");
    },
    // 验证两次密码是否一致
    samePwnFn(rule, value, callback) {
      if (value !== this.form.password) {
        callback(new Error("两次输入的密码不一致！请重新编辑。"));
      } else {
        callback();
      }
    },
    // 获取食堂列表
    async getRoomList() {
      const { data: res } = await getRoomListAPI();
      if (res.code !== 0) return this.$message.error("获取食堂列表失败！");
      this.roomList = res.data;
    },
  },
  //   =================================================以下是mounted部分=================================================
  mounted() {
    this.getRoomList();
  },
};
</script>

<style lang="less" scoped>
.reg-container {
  background: url("../../assets/images/shop.jpg") center;
  background-size: cover;
  height: 100%;

  .reg-box {
    width: 400px;

    background-color: #fff;
    border-radius: 3px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    padding: 0 30px;
    box-sizing: border-box;

    .title-box {
      height: 60px;
      text-align: center;
      line-height: 60px;
      font-size: 30px;
      font-weight: 600;
      font-family: "楷体";
      color: rgb(102, 177, 255);
    }

    .btn-reg {
      width: 100%;
    }
  }
}
</style>
